import React from "react";
import {
  createHashRouter,
  RouterProvider
} from "react-router-dom";

import Page, { ErrorPage } from "./utils/Page";

import Home from "./pages/home/Home";
import Events from "./pages/events/Events";
import Museum from "./pages/museum/Museum";
import Team from "./pages/team/Team";
import CTF from "./pages/ctf/CTF";
import CourseList from "./pages/courses/pages/List";
import CourseIndividual from "./pages/courses/pages/Individual";
import NewsList from "./pages/news/pages/List";
import NewsIndividual from "./pages/news/pages/Individual";

const router = createHashRouter([
  { path: "/", element: (<Page title=""><Home /></Page>), errorElement: (<ErrorPage />) },
  { path: "/events", element: (<Page title=""><Events /></Page>) },
  { path: "/courses", element: (<Page title=""><CourseList /></Page>) },
  { path: "/courses/:id", element: (<Page title=""><CourseIndividual /></Page>) },
  { path: "/museum", element: (<Page title=""><Museum /></Page>) },
  { path: "/news", element: (<Page title=""><NewsList /></Page>) },
  { path: "/news/:id", element: (<Page title=""><NewsIndividual /></Page>) },
  { path: "/team", element: (<Page title=""><Team /></Page>) },
  { path: "/ctf/:id", element: (<Page title=""><CTF /></Page>) }
]);

export default function Main() {
  return (
    <RouterProvider router={router} />
  );
}